import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import RedPacket from './views/RedPacket.vue'
import Demo from './views/Demo.vue'
import MoveDirection from './views/MoveDirection.vue'
import NumberInput from './views/NumberInput.vue'
import SlideBarList from './views/SlideBarList.vue'
import LazyLoad from './views/LazyLoadPage.vue'
import ProgressBar from './views/ProgressBar.vue'
import MessageDirector from './views/MessageDirector.vue'

Vue.use(Router)
const router = new Router({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes: [{
      path: '/',
      meta: { title: '' },
      name: 'home',
      component: Home
    },{
      meta: { title: '' },
      path: '/red-packet',
      name: 'redPacket',
      component: RedPacket
    },{
      meta: { title: '' },
      path: '/move-direction',
      name: 'moveDirection',
      component: MoveDirection
    },{
      path: '/demo',
      meta: { title: 'demo' },
      name: 'demo',
      component: Demo,
    },{
      meta: { title: '' },
      path: '/number-input',
      name: 'numberInput',
      component: NumberInput,
    },{
      meta: { title: '' },
      path: '/progress-bar',
      name: 'progressBar',
      component: ProgressBar,
    },{
      meta: { title: '' },
      path: '/slide-bar-list',
      name: 'slideBarList',
      component: SlideBarList,
    },{
      meta: { title: '' },
      path: '/lazyload',
      name: 'LazyLoad',
      component: LazyLoad,
    },{
      meta: { title: '' },
      path: '/message-director',
      name: 'MessageDirector',
      component: MessageDirector,
    }
  ]
})
router.beforeEach((to, from, next) => {
  document.title = to.meta.title
  next();
});
export default router
